.about-page {
  &__about-info {
    display: grid;
    grid-gap: 30px;
    padding-bottom: 150px;
    grid-template-areas:
      'bioimage bio bio'
      'job1 job1 job1'
      'job2 job2 job2'
      'job3 job3 job3';
    grid-template-columns: 1fr 1fr 1fr;

    @include responsive('tablet') {
      grid-template-areas:
        'bioimage'
        'bio'
        'job1'
        'job2'
        'job3';
      grid-template-columns: 1fr;
      text-align: center;
    }

    @include responsive(mobile) {
      padding-bottom: 250px;
    }

    p {
      text-align: justify;
    }
  }

  &__bioimage {
    grid-area: bioimage;
    margin-top: 44px;
    border-radius: 50%;
    border: 3px solid $secondary-color;

    @include responsive('tablet') {
      margin: auto;
      width: 220px;
      height: 220px;
    }
  }

  &__bio {
    grid-area: bio;
    font-size: 1.5rem;
  }

  &__job {
    grid-column: 1/3;
  }

  &__job-1 {
    grid-area: job1;
  }

  &__job-2 {
    grid-area: job2;
  }

  &__job-3 {
    grid-area: job3;
  }

  &__job {
    background: lighten($primary-color, 5);

    padding: 1.5rem;
    border-bottom: $secondary-color 5px solid;
    text-align: center;
    line-height: 1.7;
    text-align: justify;
  }

  &__resume {
    display: inline-block;
    margin: 2rem;
    font-size: 2rem;
    text-decoration: none;
    font-weight: 600;
    color: set-text-color($primary-color);
    border: 3px solid white;
    padding: 1rem 2rem;
    @include easeOut;

    @include responsive('desktop-sd') {
      font-size: 1.8rem;
    }

    @include responsive('tablet') {
      font-size: 1.6rem;
    }
    &:hover {
      background-color: $secondary-color;
      color: set-text-color($secondary-color);
      .about-page__text-secondary {
        color: set-text-color($secondary-color);
      }
    }
  }

  &__text-secondary {
    color: $secondary-color;

    @include responsive('tablet') {
      margin: 0;
      text-align: left;
    }
  }
}
